 .sectionTwo {
     display: flex;
     align-items: center;
     justify-content: space-between;
     height: 40vw;
 }

 .sectionTwo .intro {
     font-size: 2.5rem;
     margin-left: 10%;
 }

 .sectionTwo .intro .name {
     background: linear-gradient(to right, #13B0F5 0%, #E70FAA 100%);
     -webkit-background-clip: text;
     -webkit-text-fill-color: transparent;
 }

 .sectionTwo .developerImage {
     margin-right: 20%;
 }

 .sectionTwo .developerImage img {
     border-radius: 50%;
 }

 .sectionThree {
     display: flex;
     flex-wrap: wrap;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     width: 100%;
 }

 .sectionThree .sectionThreeLogo {
     display: flex;
     flex-wrap: wrap;
     align-items: center;
     justify-content: center;
 }

 .sectionThree .sectionThreeLogo img {
     padding: 4%;
     max-width: 120px;
 }

 .sectionThree .sectionThreeLogo .sectionThreeHead img {
     width: 6rem;
     margin: 4rem;
 }
 .sectionHead {
    font-size: 28px;
    color: #42466E;
    font-weight: bold;
  }
 .sectionFour {
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     padding: 10vw 0;
 }

 .sectionFour .sectionFourCardWrap {
     display: flex;
     flex-wrap: wrap;
 }

 .sectionFour .sectionFourCardWrap .colWrap {
     padding: 2vw;
     width: 26vw;
 }

 .sectionFour .sectionFourCardWrap .colWrap .cardBorder {
     display: flex;
     flex-direction: column;
     border-radius: 10px;
     overflow: hidden;
     box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
 }

 .sectionFour .sectionFourCardWrap .colWrap .cardBorder .cardImage {
     width: 100%;
     text-align: left;
 }

 .sectionFour .sectionFourCardWrap .colWrap .cardBorder .cardImage img {
     width: 100%;
     height: 16vw;
 }

 .sectionFour .sectionFourCardWrap .colWrap .cardBorder .cardContent {
     padding: 20px;
 }

 .sectionFour .sectionFourCardWrap .colWrap .cardBorder .cardContent .cardHeader {
     font-size: 20px;
     font-weight: bold;
 }

 .sectionFour .sectionFourCardWrap .colWrap .cardBorder .cardContent .operation {
     padding: 10px 0;
     text-overflow: ellipsis;
     height: 3em;
 }

 .sectionFour .sectionFourCardWrap .colWrap .cardBorder .cardFooter {
     display: flex;
     justify-content: space-between;
     padding: 20px;
 }